export default {
	name: 'ClBadge',

	props: {
		value: [String, Number],
		max: Number,
		isDot: Boolean,
		hidden: Boolean,
		type: {
			type: String,
			validator(val) {
				return ['primary', 'success', 'warning', 'info', 'error'].indexOf(val) > -1;
			}
		}
	},

	computed: {
		content() {
			if (this.isDot) return '';

			const value = this.value;
			const max = this.max;

			if (typeof value === 'number' && typeof max === 'number') {
				return max < value ? `${max}+` : value;
			}

			return value;
		}
	}
};
